import React, { Component, lazy, Suspense } from "react";
import { connect } from "react-redux";
import { reduxForm, reset } from "redux-form";
import SelectSearch from "react-select-search";
import { getUUID } from "../actions/fakturAction";
import Swal from "sweetalert2";
import { getListReturnPenjualan } from "../actions/returnPenjualanAction";
import {
  finishSend,
  getBruto,
  getGross,
  getK,
  getNetto,
  getP,
  getPkg,
  getQty,
  getRp,
  progressSend,
} from "../actions/LogicAction";
import PrintNota from "../Stocking/Component/PrintNota";
import { warning } from "../Alert";
import ReactTooltip from "react-tooltip";
import { getTotalNW } from "../actions/returnPenjualanAction";
import Skeleton from "react-loading-skeleton";
import { showModal } from "../actions/ModalAction";
import HeaderCard from "../HeaderCard";
import { getLokasi } from "../actions/lokasiAction";
import { getsalesmanList } from "../actions/salesmanAction";
import { getcustomerList } from "../actions/customerAction";
import { AxiosMasterPost } from "../Axios";
import GlobalTabel from "../GlobalComponent/GlobalTabel";

const ModalMasterData = lazy(() =>
  import("../DataMaster/Component/ModalMasterData")
);
const FormTransaksiPenjualan = lazy(() =>
  import("./Component/FormTranskasiPenjualan")
);
const maptostate = (state) => {
  return {
    noFaktur: state.provinsi.noFaktur,
    ReturnPenjualan: state.provinsi.returnPenjualan,
    listCustomer: state.provinsi.customer,
    listLokasi: state.provinsi.lokasi,
    listSales: state.provinsi.salesman,
    totalNW: state.provinsi.totalNW,
    sumNetto: state.perhitungan.sumNetto,
    sumBruto: state.perhitungan.sumBruto,
    sumPkg: state.perhitungan.sumPkg,
    sumP: state.perhitungan.sumP,
    sumK: state.perhitungan.sumK,
    sumQty: state.perhitungan.sumNetto,
    sumGross: state.perhitungan.sumGross,
    sumRp: state.perhitungan.sumRp,
    username: state.provinsi.username,
    onSend: state.provinsi.onSend,
  };
};
class ReturnPenjualan extends Component {
  constructor(props) {
    super(props);
    this.state = {
      listLokasi: [],
      listSales: [],
      listCustomer: [],
      columns: [
        {
          dataField: "kode_jenis",
          text: "Kode Barang",
        },
        {
          dataField: "kadar_cetak",
          text: "Kadar Cetak(%)",
        },
        {
          dataField: "plastik",
          text: "P",
        },
        {
          dataField: "kulit",
          text: "K",
        },
        {
          dataField: "pkg",
          text: "Bungkus(Gr)",
        },
        {
          dataField: "bruto",
          text: "Berat(Gr)",
        },
        {
          dataField: "kadar_jual",
          text: "Harga(%)",
        },
        {
          dataField: "netto",
          text: "Murni(Gr)",

          footerClasses: "sum-netto-terima-supplier",
        },
        {
          dataField: "disc_persen",
          text: "%",

          footerClasses: "sum-netto-terima-supplier",
        },
        {
          dataField: "disc",
          text: "Disc (Gr)",

          footerClasses: "sum-netto-terima-supplier",
        },
        {
          dataField: "netto_disc",
          text: "Netto Disc(Gr)",
        },
        {
          dataField: "Aksi",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row, rowIndex) => {
            return (
              <div className="text-center">
                <button
                  data-tip
                  data-for="hapus"
                  className="btn btn-danger mr-2"
                  onClick={() => this.deleteRow(rowIndex, row.netto_disc)}
                >
                  <ReactTooltip id="hapus" type="dark" effect="solid">
                    <span>Hapus</span>
                  </ReactTooltip>
                  <i className="fa fa-trash mr-1"></i>
                </button>
                <br />
                {/* <button
                  data-tip
                  data-for="edit"
                  className="btn btn-warning mr-2"
                  data-toggle="modal"
                  data-target="#editModal"
                  onClick={() => this.setEdit(row)}
                >
                  <ReactTooltip id="edit" type="dark" effect="solid">
                    <span>Edit</span>
                  </ReactTooltip>
                  <i className="fa fa-edit mr-1"></i>
                </button> */}
              </div>
            );
          },
        },
      ],
      discGlobalGr: 0,
      discGlobalPr: 0,
      totalNWExt: 0,
      noBon: "",
      namaCustomer: "",
      lokasi: "",
      sales: "",
      namaToko: "",
      tanggal: "",
      data: "",
    };
  }

  deleteRow(rowIndex, dihapus) {
    Swal.fire({
      title: "Anda Yakin ?",
      text: "Anda Akan Menghapus Data Ini ?",
      icon: "warning",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Yes, delete it!",
    }).then((result) => {
      if (result.isConfirmed) {
        const array = JSON.parse(localStorage.getItem("ReturnPenjualan")) || [];
        array.splice(rowIndex, 1);
        localStorage.setItem("ReturnPenjualan", JSON.stringify(array));
        var awal = localStorage.getItem("GrandTotalNetto");
        var hasil = parseFloat(awal) - parseFloat(dihapus);
        localStorage.setItem("GrandTotalNetto", parseFloat(hasil).toFixed(3));
        Swal.fire({
          text: "Berhasil",
          timer: 2000,
          showConfirmButton: false,
          position: "top-right",
          icon: "success",
        })
          .then(() => this.props.dispatch(getTotalNW()))
          .then(() => this.props.dispatch(getListReturnPenjualan()))
          .then(() =>
            this.setState({
              GrandTotalNetto: (
                parseFloat(localStorage.getItem("GrandTotalNetto") || 0) - 0
              ).toFixed(3),
            })
          );
      }
    });
  }
  componentDidMount() {
    this.props.dispatch(getLokasi());
    this.props.dispatch(getsalesmanList());
    this.props.dispatch(getcustomerList());
    this.props.dispatch(getUUID());
    this.props.dispatch(getListReturnPenjualan());
    this.props.dispatch(getTotalNW());
    this.props.dispatch(getBruto("ReturnPenjualan"));
    this.props.dispatch(getNetto("ReturnPenjualan"));
    this.props.dispatch(getPkg("ReturnPenjualan"));
    this.props.dispatch(getRp("ReturnPenjualan"));
    this.props.dispatch(getGross("ReturnPenjualan"));
    this.props.dispatch(getP("ReturnPenjualan"));
    this.props.dispatch(getK("ReturnPenjualan"));
    this.props.dispatch(getQty("ReturnPenjualan"));
    this.setState({
      GrandTotalNetto: (
        parseFloat(localStorage.getItem("GrandTotalNetto") || 0) - 0
      ).toFixed(3),
    });
  }

  handleSubmit(data) {
    var barang = data.kodeBarang
      ? data.kodeBarang
      : "DEFAULT|DEFAULT|DEFAULT|DEFAULT";
    var result_explode = barang.split("|");
    var urutan = JSON.parse(localStorage.getItem("ReturnPenjualan")) || [];
    var simpan = {
      no_urut: (urutan.length || 0) + 1,
      netto_disc: data.nettoDiscGlobalExt,
      gross: parseFloat(data.gross), //berat + bungkus
      netto: parseFloat(data.netto), //murni
      keterangan: data.keterangan,
      kode_barcode: data.kode_barcode || "-",
      qty: 1,
      kulit: parseFloat(data.k),
      ongkos_rp: parseFloat(data.rp === undefined ? 0 : data.rp), //ongkos
      kadar_cetak: result_explode[2],
      disc_persen: data.discGlobalPr,
      kadar_jual: parseFloat(data.kadarTransaksi), //harga%
      kadar_modal: result_explode[4], //harga%
      kode_jenis: result_explode[0], //kodebarang
      plastik: parseFloat(data.p),
      disc: data.discGlobalGr,
      pkg: parseFloat(data.pkg),
      bruto: parseFloat(data.bruto), //berat
    };
    var array = JSON.parse(localStorage.getItem("ReturnPenjualan")) || [];
    array.push(simpan);
    var GrandTotalNetto = array
      .map((list) => parseFloat(list.netto_disc))
      .reduce((a, b) => a + b, 0)
      .toFixed(3);
    localStorage.setItem("GrandTotalNetto", GrandTotalNetto);
    localStorage.setItem("ReturnPenjualan", JSON.stringify(array));
    Swal.fire({
      position: "top-end",
      icon: "success",
      text: "Data Berhasil Ditambahkan!",
      showConfirmButton: false,
      timer: 1500,
    });
    this.props.dispatch(getListReturnPenjualan());
    this.props.dispatch(getTotalNW());
    this.props.dispatch(reset("FormTransaksiPenjualan"));

    this.props.dispatch(getNetto("ReturnPenjualan"));
    this.setState({
      discGlobalGr:
        (parseFloat(0) / 100) *
        parseFloat(localStorage.getItem("GrandTotalNetto")),
      discGlobalPr: 0,
      GrandTotalNetto: (
        parseFloat(localStorage.getItem("GrandTotalNetto")) - 0
      ).toFixed(3),
    });
  }

  transaksiPenjualanSubmit() {
    if (localStorage.getItem("ReturnPenjualan") === null) {
      warning("Barang masih kosong, Silahkan tambah barang");
      return false;
    } else if (this.state.noBon === "") {
      warning("Mohon isi Nomor bon");
      return false;
    } else if (this.state.namaToko === "") {
      warning("Mohon pilih toko");
      return false;
    } else if (this.state.discGlobalPr === "") {
      warning("Mohon isi Discount");
      return false;
    } else if (this.state.discGlobalGr === "") {
      warning("Mohon isi Discount Gr");
      return false;
    } else if (this.state.totalNWExt === 0) {
      warning("Mohon isi Grand Total");
      return false;
    } else if (this.state.tanggal === "") {
      warning("Mohon isi Tanggal");
      return false;
    }
    var simpan = {
      no_bon: this.state.noBon.toUpperCase(),
      no_retur: this.props.noFaktur.toUpperCase(),
      tanggal: this.state.tanggal,
      kode_customer: this.state.kodeCustomer.toUpperCase(),
      detail_barang: JSON.parse(localStorage.getItem("ReturnPenjualan")),
      total_netto: this.props.totalNW,
      disc_global_persen: this.state.discGlobalPr,
      disc_global: this.state.discGlobalGr,
      total_netto_disc: this.state.GrandTotalNetto,
      total_netto_disc_ext: this.state.totalNWExt,
    };
    console.log(JSON.stringify(simpan));
    this.props.dispatch(progressSend());
    // INISIALISASI AUTOTABLE
    const tableRows = [];
    var table = JSON.parse(localStorage.getItem("ReturnPenjualan"));
    // for each ticket pass all its data into an array
    table.forEach((ticket, i) => {
      const ticketData = [
        ++i,
        ticket.kadar_cetak,
        ticket.kode_jenis,
        parseFloat(ticket.bruto).toFixed(3),
        ticket.kadar_jual,
        parseFloat(ticket.disc).toFixed(3),
        parseFloat(ticket.netto).toFixed(3),
        ticket.pkg,
        ticket.qty,
        parseFloat(ticket.ongkos_rp).toLocaleString("id-ID"),
        "",
      ];
      // push each tickcet's info into a row
      tableRows.push(ticketData);
    });
    const footer = [
      "",
      "",
      "",
      table
        .map((list) => parseFloat(list.bruto))
        .reduce((a, b) => a + b, 0)
        .toFixed(3),
      "",
      table
        .map((list) => parseFloat(list.disc))
        .reduce((a, b) => a + b, 0)
        .toFixed(3),
      table
        .map((list) => parseFloat(list.netto))
        .reduce((a, b) => a + b, 0)
        .toFixed(3),
      table.map((list) => parseFloat(list.pkg)).reduce((a, b) => a + b, 0),
      table.map((list) => parseFloat(list.qty)).reduce((a, b) => a + b, 0),
      table
        .map((list) => parseFloat(list.ongkos_rp))
        .reduce((a, b) => a + b, 0)
        .toLocaleString("id-ID"),
      this.state.totalNWExt,
    ];
    tableRows.push(footer);
    console.log(JSON.stringify(tableRows));
    var columTable = [
      "No",
      "Kadar",
      "Kode",
      "Bruto \n(Gr)",
      "Harga \n(%)",
      "Diskon",
      "Netto \n(Gr)",
      "Pkg",
      "Qty",
      "Ongkos \n(Rp)",
      "Grand Total\nNetto Ext",
    ];
    // AKHIR INISIALISASI AUTOTABLE
    AxiosMasterPost("trx-customer/retur-customer", simpan)
      .then(() =>
        Swal.fire({
          position: "top-end",
          icon: "success",
          text: "Data Berhasil Ditambahkan!",
          showConfirmButton: false,
          timer: 1500,
        })
          .then(() =>
            PrintNota(
              "no Trx",
              this.props.noFaktur.toUpperCase(),
              "Tanggal",
              this.state.tanggal,
              "No Bon",
              this.state.noBon.toUpperCase(),
              "Sales",
              this.state.namaToko.toUpperCase(),
              "ADMIN",
              this.state.tanggal,
              "",
              columTable,
              "Transaksi Penjualan",
              tableRows
            )
          )
          .then(() => this.props.dispatch(finishSend()))
          .then(() => localStorage.removeItem("ReturnPenjualan"))
          .then(() => localStorage.removeItem("GrandTotalNetto"))
          .then(() => localStorage.removeItem("noFaktur"))
          .then(() => this.props.dispatch(getUUID()))
          .then(() => this.props.dispatch(getTotalNW()))
          .then(() => this.props.dispatch(getListReturnPenjualan()))
          .then(() =>
            this.setState({
              noBon: "",
              namaCustomer: "",
              lokasi: "",
              sales: "",
              namaToko: "",
              tanggal: "",
              toko: "",
              GrandTotalNetto: 0,
            })
          )
          .then(() => document.getElementById("myForm").reset())
      )
      .catch((err) =>
        Swal.fire({
          position: "top-end",
          icon: "error",
          text: `${err.response.data}`,
          showConfirmButton: false,
          timer: 1500,
        }).then(() => this.props.dispatch(finishSend()))
      );
    console.log(JSON.stringify(simpan));
  }
  inputChange = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };

  inputGrandTotal = (input) => (e) => {
    var GrandNetto = this.props.sumGross - parseFloat(this.state.discGlobalGr);
    var grandTotal = e.target.value;
    console.log(GrandNetto + "-" + grandTotal);
    if (GrandNetto - grandTotal < -1) {
      warning("Selisih lebih dari 1");
      e.target.value = "";
      return false;
    } else {
      if (GrandNetto - grandTotal <= 1) {
        this.setState({
          [input]: e.target.value,
        });
      } else {
        warning("Selisih lebih dari 1");
        e.target.value = "";
        return false;
      }
    }
  };
  discount = (input) => (e) => {
    var hasil =
      (parseFloat(e.target.value) / 100) *
      parseFloat(localStorage.getItem("GrandTotalNetto"));
    this.setState({
      discGlobalGr: hasil.toFixed(3),
      discGlobalPr: e.target.value,
      GrandTotalNetto: (
        parseFloat(localStorage.getItem("GrandTotalNetto")) - hasil.toFixed(3)
      ).toFixed(3),
    });
  };
  onSubmit(data, input) {
    this.setState({
      [input]: data,
    });
  }
  onSubmitToko(data, input) {
    var split = data.split("|");
    this.setState({
      kodeCustomer: split[0],
      namaCustomer: split[1],
      namaToko: split[2],
      toko: data,
    });
  }

  render() {
    return (
      <div className="container-fluid">
        <div className="col-lg-12">
          <div className="card">
            <HeaderCard title="Transaksi Penjualan" />
            <div className="card-body">
              <div className="row">
                <div className="col-lg-9"></div>
                {/* <div className="col-lg-3">
                  <div className="text-right">
                    <Link to="/EditForm/ReturnPenjualan">
                      <button
                        className="btn btn-warning"
                        // data-target="#editModal"
                        // data-toggle="modal"
                      >
                        Edit Transaksi Penjualan
                      </button>
                    </Link>
                  </div>
                </div> */}
                <div className="col-lg-12">
                  <br />
                </div>

                <div className="col-lg-12">
                  <form
                    id="myForm"
                    onKeyDown={(event) => {
                      return event.key !== "Enter";
                    }}
                  >
                    <div className="row">
                      <div className="col-lg-4 col-md-4">
                        <div className="form-group">
                          <label htmlFor="">Tanggal</label>
                          <input
                            name="tanggal"
                            type="date"
                            label="Tanggal"
                            className="form-control"
                            onChange={this.inputChange("tanggal")}
                            value={this.state.tanggal}
                          />
                        </div>
                      </div>
                      <div className="col-lg-4 col-md-4">
                        <div className="form-group">
                          <label htmlFor="">No Bon</label>
                          <input
                            name="noBon"
                            type="text"
                            label="No Bon"
                            className="form-control"
                            onChange={this.inputChange("noBon")}
                            value={this.state.noBon}
                          />
                        </div>
                      </div>
                      <div className="col-lg-4 col-md-4"></div>
                      {/* <div className="col-lg-3 col-md-3">
                        <div className="form-group">
                          <label htmlFor="">Lokasi</label>
                          <SelectSearch
                            options={this.props.listLokasi.map((obj) => {
                              var payload = {
                                value: obj.kode_lokasi,
                                name: obj.nama_lokasi,
                              };
                              return payload;
                            })}
                            search
                            placeholder="Silahkan Pilih"
                            onChange={(data) => this.onSubmit(data, "lokasi")}
                            value={this.state.lokasi}
                          />
                        </div>
                      </div>
                      <div className="col-lg-3 col-md-3">
                        <div className="form-group">
                          <label htmlFor="">Sales</label>
                          <SelectSearch
                            options={this.props.listSales.map((obj) => {
                              var payload = {
                                value: obj.kode_sales,
                                name: obj.nama_sales,
                              };
                              return payload;
                            })}
                            search
                            placeholder="Silahkan Pilih"
                            onChange={(data) => this.onSubmit(data, "sales")}
                            value={this.state.sales}
                          />
                        </div>
                      </div> */}
                      <div className="col-lg-3 col-md-3">
                        <div className="form-group">
                          <label htmlFor="">Nama Toko</label>
                          <SelectSearch
                            options={this.props.listCustomer.map((obj) => {
                              var payload = {
                                value:
                                  obj.kode_customer +
                                  "|" +
                                  obj.nama_customer +
                                  "|" +
                                  obj.nama_toko,
                                name: obj.nama_toko,
                              };
                              return payload;
                            })}
                            search
                            placeholder="Silahkan Pilih"
                            onChange={(data) =>
                              this.onSubmitToko(data, "namaToko")
                            }
                            onBlur={(data) => this.onSubmit(data, "toko")}
                            value={this.state.toko}
                          />
                        </div>
                      </div>
                      <div className="col-lg-3 col-md-3">
                        <div className="form-group">
                          <label htmlFor="">Customer</label>
                          <input
                            name="customer"
                            type="text"
                            className="form-control"
                            label="Customer"
                            onChange={this.inputChange("namaCustomer")}
                            defaultValue={this.state.namaCustomer}
                          />
                        </div>
                      </div>
                      <div className="col-lg-3 mb-3">
                        <label htmlFor="">Total NW</label>
                        <input
                          name="totalNW"
                          type="number"
                          className="form-control"
                          min={0}
                          step={0.01}
                          label="totalNW"
                          value={this.props.totalNW}
                          readOnly={true}
                        />
                      </div>
                      <div className="col-lg-3">
                        <label htmlFor="">Disc Global (%)</label>
                        <input
                          name="discGlobal"
                          type="number"
                          className="form-control"
                          min={0}
                          step={0.001}
                          label="discGlobal"
                          onChange={this.discount("discGlobalPr")}
                          value={this.state.discGlobalPr}
                        />
                      </div>
                      <div className="col-lg-3">
                        <label htmlFor="">Disc (Gr)</label>
                        <input
                          name="discGlobal"
                          type="number"
                          className="form-control"
                          label="discGlobal"
                          value={this.state.discGlobalGr}
                          readOnly={true}
                        />
                      </div>
                      <div className="col-lg-3">
                        <label htmlFor="">Grand Total Netto</label>
                        <input
                          name="totalNW"
                          type="number"
                          className="form-control"
                          label="totalNW"
                          value={this.state.GrandTotalNetto}
                          readOnly={true}
                        />
                      </div>
                      <div className="col-lg-3">
                        <label htmlFor="">Grand Total Netto Ext</label>
                        <input
                          name="totalNWExt"
                          type="text"
                          className="form-control"
                          label="totalNWExt"
                          onChange={this.inputChange("totalNWExt")}
                          // onBlur={this.inputGrandTotal("totalNWExt")}
                        />
                      </div>
                    </div>
                  </form>
                </div>
                <div className="col-lg-12 col-md-12 mt-3">
                  <div className="row">
                    <div className="col-lg-6"></div>
                    <div className="col-lg-6">
                      <div className="text-right">
                        <button
                          className="btn btn-primary"
                          onClick={() => this.props.dispatch(showModal())}
                        >
                          Tambah Barang
                          <i class="fa fa-plus ml-3" aria-hidden="true"></i>
                        </button>
                      </div>
                    </div>
                  </div>
                </div>
                <div className="col-lg-12 mt-5">
                  <GlobalTabel
                    keyField="id"
                    data={this.props.ReturnPenjualan}
                    columns={this.state.columns}
                  />
                  <div className="text-right">
                    <button
                      className="btn btn-primary"
                      disabled={this.props.onSend}
                      onClick={() => this.transaksiPenjualanSubmit()}
                    >
                      {this.props.onSend ? (
                        <span
                          className="spinner-border spinner-border-sm mr-3"
                          aria-hidden="true"
                        ></span>
                      ) : null}
                      Simpan
                      <i class="fa fa-paper-plane ml-3" aria-hidden="true"></i>
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <ModalMasterData
          content={
            <Suspense
              fallback={<Skeleton width={"100%"} height={50} count={4} />}
            >
              <FormTransaksiPenjualan
                onSubmit={(data) => this.handleSubmit(data)}
              />
            </Suspense>
          }
          title="Tambah Data"
        />
      </div>
    );
  }
}

ReturnPenjualan = reduxForm({
  form: "ReturnPenjualan",
  enableReinitialize: true,
})(ReturnPenjualan);
export default connect(maptostate, null)(ReturnPenjualan);
